body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.red{
    color: red;
}
img{
    width: 100%;
    height: 40vh;
}
.h-sub{
    margin:0;
}
.title-container{
    width: 100%;
    background-color: #000000db;
    color: white;
    padding-bottom: 14px;
    position: absolute;
    height: 10vh;
    top: 28vh;
}
.title-container p{
    padding-left: 16px;
}
.title-container .h-top{
    font-size: 1.7rem;
    margin: 0;
    padding-top: 5px;
}
/*Using important to overwrite material ui*/
.articles-list{
    padding-top: 0 !important;
    margin-top: -4px !important;
}
.session-divider{
    color: white;
    padding: 3px 16px;
    width: 88%;
}
.session-divider.today{
    background-color: red;
}
.session-divider.world{
    background-color: #8BC34A;
}
.session-divider.currency{
    background-color: #00BCD4;
}
a{
    text-decoration: none;
}

.content{
    padding:5px 16px;
    margin-bottom: 10px;
}
.share-container{
    margin-top: 5px;
    text-align: center;
}
.share-container i{
    font-size: 2rem;
}
.share-container p{
    margin-top: 0;
}
.appbar{
    background-color: red;
    color: white;
    position: fixed !important;
    width: 100%;
    top: 0;
}
.appbar span{
    font-size: 1.3rem;
}
#markets-page-container .session-divider.currency{
    margin-top: 20%;
}
#market-header{
    margin-top: 20px;
    margin-left: 20px;
    font-size: 1.5rem;
}
.market-item{
    display: flex;
    justify-content: space-between !important;
}
.market-tag{
    display: flex;
    justify-content: space-around;
    font-size: 12px;
    font-weight: 600;
    background-color: red;
    padding: 5px;
    margin-top: 3px;
    color: white;
}
.market-tag div{
    margin-left: 7px;
}
